<template>
    <!--  <signature :src="'./signature.svg'"></signature> -->
    <div ref="svg" class="signature" @load="animate" v-show="show"></div>
</template>

<script>
    export default {
        props: ["src"],
        data() {
            return {
                show: false,
            };
        },
        async mounted() {
            this.$refs.svg.innerHTML = await fetch(this.src).then((res) => res.text());
            this.animate();
        },
        methods: {
            animate() {
                let that = this;

                setTimeout(() => {
                    this.show = true;
                    this.$anime({
                        targets: [...that.$refs.svg.querySelectorAll("path")].reverse(),
                        strokeDashoffset: [this.$anime.setDashoffset, 0],
                        easing: "easeInOutSine",
                        fill: "#000",
                        duration: 500,
                        delay: function(el, i) {
                            return i * 50;
                        },

                        loop: false,
                        complete: (anim) => {
                            that.$emit("animationend");
                        },
                    });
                }, 100);
            },
        },
    };
</script>
<style lang="less" scoped>
    .signature {
        width: 30vw;
        svg {
            overflow: visible;
        }
    }
</style>
