import Vue from "vue";
// 导入自己需要的组件
import {
    Loading,
    Avatar,
    MenuItem,
    MenuItemGroup,
    Link,
    Radio,
    RadioGroup,
    RadioButton,
    Switch,
    Menu,
    Cascader,
    Button,
    Carousel,
    Dialog,
    Row,
    Col,
    Drawer,
    Submenu,
    Image,
    Message,
    Card,
    CarouselItem,
    Option,
    Select,
    Tabs,
    TabPane,
} from "element-ui";
const needArray = [
    Avatar,
    MenuItem,
    MenuItemGroup,
    Link,
    Switch,
    Menu,
    Radio,
    RadioGroup,
    RadioButton,
    Cascader,
    Button,
    Carousel,
    Dialog,
    Row,
    Col,
    Drawer,
    Submenu,
    Image,
    Card,
    CarouselItem,
    Option,
    Select,
    Tabs,
    TabPane,
];
const element = {
    install: function(Vue) {
        needArray.forEach((i) => Vue.use(i));
    },
};
Vue.use(Loading.directive);

Vue.prototype.$loading = Loading.service;
Vue.prototype.$message = Message;
export default element;
