<template>
    <div id="app">
        <loadingAnimation ref="loading" @animationend="loaded" v-show="loadingVisiable" clas="mask"></loadingAnimation>
        <div class="header">
            <MENU class="btn-fix"></MENU>
        </div>

        <transition name="first" enter-active-class="animate__animated animate__backInLeft" leave-active-class="animate__animated animate__backOutRight">
            <router-view class="main"></router-view>
        </transition>
    </div>
</template>

<script>
    import loadingAnimation from "@/views/loadingAnimation.vue";
    import MENU from "@/views/menu.vue";
    import { animate } from "@/function/animate.js";

    export default {
        components: { MENU, loadingAnimation },
        data() {
            return {
                show: true,
                animate: false,
                loadingAnimateEnd: false,
                loadingVisiable: true, // 这是开场动画的开关
            };
        },
        mounted() {
            document.body.scrollIntoView();
            document.body.style.overflow = "hidden";
            this.show = this.$route.params.project ? false : true;
        },
        methods: {
            loaded() {
                this.$refs.loading.$el.classList.add("animate__animated", "animate__fadeOut", "animate__delay-1s");
                this.$refs.loading.$el.addEventListener(
                    "animationend",
                    function() {
                        this.$refs.loading.$el.classList.remove("animate__animated", "animate__fadeOut", "animate__delay-1s");
                        this.loadingVisiable = false;
                        document.body.style.overflow = "scroll";
                    }.bind(this)
                );
                this.loadingAnimateEnd = true;
            },
            test() {
                animate(this.$refs.test.$el, "bounce");
            },
        },
        watch: {
            "$route.params.project": function(val) {
                this.show = val ? false : true;
            },
        },
    };
</script>
<style lang="less">
    .mask {
        z-index: 101;
    }

    #app {
        font-family: Avenir, Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        height: 100vh;
        width: 100vw;
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
    }

    .main {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-items: center;
        margin: 0 auto;
        height: 100%;
    }
    .el-submenu__title {
        font-size: 25px !important;
    }

    ::-webkit-scrollbar {
        /*滚动条整体样式*/
        width: 0px; /*高宽分别对应横竖滚动条的尺寸*/
        height: 0px;
        transition: 0.5s;
    }

    ::-webkit-scrollbar-thumb {
        /*滚动条里面小方块*/
        border-radius: 10px;
        background: #bdbdbd;
    }
    ::-webkit-scrollbar-track {
        /*滚动条里面轨道*/
        border-radius: 10px;
    }
</style>
